.row {
  margin-left: 20px;
  color: whitesmoke;
  font-size: 1rem;
}
.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 0px;
}
.row__posters::-webkit-scrollbar {
  display: none;
}
.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  margin-right: 10px;
  transition: transform 500ms;
}
.row__poster:hover {
  transform: scale(1.08);
}
.row__posterlarge {
  max-height: 250px;
}
row__posterlarge:hover {
  transform: scale(1.09);
}
